<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval: 40,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* Inspired by http://strml.net/
* Â§ßÂÆ∂Â•ΩÔºåÊàëÊòØYangKai
* ÊàëÊ≠£Âú®ÊâæÂ∑•‰ΩúÂì¶Ôºå‰Ω†‰ª¨ÂèØ‰ª•ÈöèÊó∂ËÅîÁ≥ªÊàë!ÊàëÂ∏åÊúõÂæóÂà∞Êú∫‰ºöÔºåÂñúÊ¨¢ÂíåÂêÑ‰ΩçÊ≤üÈÄö‰∫§ÊµÅÔºåËØ∑Â§öÂ§öÊåáÊïôÔºÅÈÇ£ÊàëÂÅö‰∏Ä‰ªΩÁÆÄÂéÜÂêß
* ËØ¥ÂÅöÂ∞±ÂÅöÔºåÊàëÊù•ÂÜô‰∏Ä‰ªΩÁÆÄÂéÜÔºÅ
*/

/* È¶ñÂÖàÁªôÊâÄÊúâÂÖÉÁ¥†Âä†‰∏äËøáÊ∏°ÊïàÊûú */
* {
  transition: all .3s;
}
/* ÁôΩËâ≤ËÉåÊôØÂ§™ÂçïË∞É‰∫ÜÔºåÊàë‰ª¨Êù•ÁÇπËÉåÊôØ */
html {
  color: rgb(222,222,222); background: rgb(0,43,54);
}
/* ÊñáÂ≠óÁ¶ªËæπÊ°ÜÂ§™Ëøë‰∫Ü */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 45vw; height: 90vh;
}
/* ‰ª£Á†ÅÈ´ò‰∫Æ */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/* Âä†ÁÇπ 3D ÊïàÊûúÂëó */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/* Êé•‰∏ãÊù•ÊàëÁªôËá™Â∑±ÂáÜÂ§á‰∏Ä‰∏™ÁºñËæëÂô® */
.resumeEditor{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 48vw; height: 90vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/* Â•Ω‰∫ÜÔºåÊàëÂºÄÂßãÂÜôÁÆÄÂéÜ‰∫Ü */


`,
          `
/* Ëøô‰∏™ÁÆÄÂéÜÂ•ΩÂÉèÂ∑ÆÁÇπ‰ªÄ‰πà
 * ÂØπ‰∫ÜÔºåËøôÊòØ Markdown Ê†ºÂºèÁöÑÔºåÊàëÈúÄË¶ÅÂèòÊàêÂØπ HR Êõ¥ÂèãÂ•ΩÁöÑÊ†ºÂºè
 * ÁÆÄÂçïÔºåÁî®ÂºÄÊ∫êÂ∑•ÂÖ∑ÁøªËØëÊàê HTML Â∞±Ë°å‰∫Ü
 */
`
          ,
          `
/* ÂÜçÂØπ HTML Âä†ÁÇπÊ†∑Âºè */
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '‚Ä¢';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`],
        currentMarkdown: '',
        fullMarkdown: `Êù®ÂáØ
----

 Â§öÂπ¥ÂâçÁ´ØÂ∑•‰ΩúÁªèÈ™åÂì¶ÔºÅÔºÅÔºÅÂÖ∑‰ΩìËØ∑ÁúãÊàëÁöÑÊãõËÅòÁΩëÁ´ôÁöÑÁÆÄÂéÜÈòøÔºåÈÇ£‰∏äÈù¢ÂÜôÁöÑÊØîËæÉÊ∏ÖÊ•öÂì¶ÔºÅÔºÅÔºÅ

ÊäÄËÉΩ
----

* ÂâçÁ´ØÂºÄÂèë:Html+css+js+vue Áõ∏ÂØπÁÜüÊÇâÔºåÂÖ∂‰ªñÂæÆ‰ø°Â∞èÁ®ãÂ∫èÔºåreactÊòØÁü•ÈÅì‰∫ÜËß£ËøáÔºåÂÜôËøá‰ª£Á†ÅÈ¢ùÔºåÂ±û‰∫élowÁöÑÔºåÂÖ∂‰ªñÂ∞±ÊòØÂ±û‰∫éÁúãËøáËµÑÊñôÔºå‰ΩÜÊòØÊµÖÂ∞ùËæÑÊ≠¢È¢ùÔºåÂÖ∂ÂÆûÂ≠¶Âà∞Áé∞Âú®ÂæàÂ§ö‰∏çÊáÇÂ∫ïÂ±ÇÂéüÁêÜÁöÑÔºåÂèπÊ∞î!!!
Âè§‰∫∫‰∫ëÔºåÁü•‰πã‰∏çÊ±ÇÁîöËß£ÔºåËØ¥ÁöÑÂ∞±ÊòØÁ†ÅÂÜú‰∫ÜÔºå‰ºöÊê¨ÔºåÁü•ÈÅìÊÄé‰πàÊê¨ÔºåÂ∞±ÊòØËØ¥‰∏çÊ∏ÖÂéüÁêÜÔºåÊôãÂçá‰∏∫‰∏ä‰∏ÄÂ±ÇÊ¨°ÔºåÈúÄË¶ÅÊΩúÂøÉ‰øÆÁÇº‰∫Ü„ÄÇ‰øÆÁÇºÂÜÖÂäõÔºàÂü∫Êú¨ÂéüÁêÜÔºâÂ§ñÂä†ÔºàÂàÄÊû™Ê£çÊ£íüó°ÔºâÔºàÊ°ÜÊû∂Â∑•ÂÖ∑ÔºâÊâçËÉΩÂú®ËøôÂâçÁ´Ø‰∏ñÁïåÂ≠òÊ¥ªÁöÑÊõ¥‰πÖÔºåÊúâÂπ∏ÁúãÂà∞ÊàëËøôÂÜÖÂÆπÁöÑÔºåËØ∑Êé®ËçêÈù¢ËØïÂì¶ÔºÅ


Â∑•‰ΩúÁªèÂéÜ
----
ÊãõËÅòÁÆÄÂéÜÂèØ‰ª•ÁúãÂà∞Âì¶ÔºåÂ∑•‰ΩúËÆ©ÊàëÁßØÁ¥ØÊõ¥Â§ö!


ÈìæÊé•
----

* [GitHub](https://github.com/yangkaiyangyi)
> Â¶ÇÊûú‰Ω†ÂñúÊ¨¢Ëøô‰∏™ÊïàÊûúÔºåFork [*](https://github.com/yangkaiyangyi/resume)ÔºåÊâìÈÄ†‰Ω†Ëá™Â∑±ÁöÑÁÆÄÂéÜÔºÅ
`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)
        await this.progressivelyShowResume()
        await this.progressivelyShowStyle(1)
        await this.showHtml()
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) { return }
            // ËÆ°ÁÆóÂâç n ‰∏™ style ÁöÑÂ≠óÁ¨¶ÊÄªÊï∞
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }
</style>



